
.blog__card{
    width: 100%;
    gap: 20px;
    
    display: flex;
  }
  
  .blog__card-img{
    max-width: 230px;
  }
  .blog__card-img img{
    width: 100%;
    
  }
  .Blog{
    color: black;
  }
  .blog__card-txt1
  {
  
    display: flex;
    gap: 10px;
    align-items: center;
  }
  .blog__card-txt1 p {
    margin-bottom: 0px;
  }
  .blog__card-txt h2{
  padding-top: 7px;
  font-size: 19px;
  font-weight: 700;
  
  }


.blog__card-txt p {
    margin-bottom: 0px;
    font-size: 14px;

}

.blog__card-txt h2, p {
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;

}
.blog__card-txt h2:hover {
    color: rgb(92, 92, 92);
    transform: scale(1.01);

    
}