header {
    height: 80vh;
    background-color: #0050B3 !important;
    display: flex;
    justify-content: center; /* Центрирование по горизонтали */
    align-items: center; /* Центрирование по вертикали */
}

.Header__carusel {
    display: flex;
    justify-content: center;
